@import url(https://fonts.googleapis.com/css2?family=Roboto:wght@300;400;500;700&display=swap);
.container{width:90%;margin:0 auto;padding:20px}@media screen and (min-width: 960px){.container{width:70%;max-width:1500px}}section{padding:80px 0px;border-bottom:3px solid #ddd8d6}@media screen and (min-width: 960px){section{padding:200px 0px}}section img{display:block;margin:0 auto}.title{font-size:1.6em;font-weight:Bold 400;color:#9d9d9d;text-align:center;padding-top:50px}@media screen and (min-width: 960px){.title{text-align:left;padding:0}}#header{padding:0;background-color:#eadbcc;border-bottom:none}#info{background-color:#eadbcc}#info p{font-size:2.2em;color:#707070}@media screen and (min-width: 960px){#info{margin-top:150px}#info p{font-size:3em}}#about{background-color:#eaeaea}#about p{font-size:2em}#about .about-description{max-width:400px;margin:0 auto}@media screen and (min-width: 960px){#about .about-description{margin:0}}#skills{background-color:#eadbcc}#skills .icon-box{font-size:0.8em;text-align:center;margin-bottom:10px}@media screen and (min-width: 960px){#skills .icon-box{max-width:80px}}#skills .iconify{width:50px;height:50px;padding:5px;margin-bottom:10px;border-radius:7%;background-color:#f2eaea;box-shadow:0 1px 2px #888888}#works{background-color:#d0c2c2}#works .title{color:#a08888}#works .card-box{max-width:300px;max-height:350px;min-height:300px;margin:0 auto;border-radius:3%}#works .card-box p{font-size:1.2em;font-weight:bold;color:#a08888}#works .card-description{display:block;margin-bottom:8px;color:#707070}#works .card-chip{color:#9d9d9d;margin-right:5px;margin-bottom:3px}#works a,#works .iconify{color:#a08888;text-decoration:none}#works a:hover,#works .iconify:hover{color:#c6bcb0}#footer{min-height:100px;padding:0;background-color:#867e7a;border-bottom:none}

body{margin:0;background-color:#eadbcc;font-family:'Century Gothic', 'Roboto', 'ヒラギノ角ゴシック', '游ゴシック体', 'Osaka-Mono', sans-serif;color:#707070;font-size:1em}

